import "./style.scss";

function polarToCartesian(centerX, centerY, radius, angleInDegrees, angleOffset) {
  const angleInRadians = ((angleInDegrees - 90 + angleOffset) * Math.PI) / 180.0;

  return {
    x: centerX + (radius * Math.cos(angleInRadians)),
    y: centerY + (radius * Math.sin(angleInRadians)),
  };
}

function createPathDefinition(x, y, radius, startAngle, endAngle, angleOffset) {
  const start = polarToCartesian(x, y, radius, endAngle, angleOffset);
  const end = polarToCartesian(x, y, radius, startAngle, angleOffset);

  const largeArcFlag = endAngle - startAngle <= 180 ? "0" : "1";

  const d = [
    "M", start.x, start.y,
    "A", radius, radius, 0, largeArcFlag, 0, end.x, end.y,
  ].join(" ");

  return d;
}

function createPath({ angle, angleOffset, stroke }) {
  const path = document.createElementNS("http://www.w3.org/2000/svg", "path");

  const d = createPathDefinition(60, 60, 58, 0, angle, angleOffset);

  path.setAttribute("stroke", stroke);
  path.setAttribute("fill", "transparent");
  path.setAttribute("stroke-width", "4");
  path.setAttribute("d", d);

  return path;
}

function parseChartValues(legendNames) {
  const values = legendNames.map((name) => {
    const el = document.querySelector(`.js-pie-chart__legend-${name}`);
    const value = parseInt(el.attributes["data-value"].value, 10);
    return { name, value };
  });

  const sumValues = values.reduce((acc, item) => item.value + acc, 0);

  const items = values.map((item) => {
    const angle = (item.value / sumValues) * 360;
    return {
      ...item,
      angle,
    };
  }).filter(({ value }) => value > 0);

  return { sumValues, items };
}

function createGroupOfPaths(items) {
  const chartGroupEl = document.createElementNS("http://www.w3.org/2000/svg", "g");

  let globalOffset = 0;
  items.forEach((item) => {
    const path = createPath({
      angle: item.angle - 2,
      angleOffset: globalOffset + 1,
      stroke: `url(#${item.name}-color)`,
    });

    globalOffset += item.angle;
    chartGroupEl.appendChild(path);
  });

  return chartGroupEl;
}

export default function drawPieChart() {
  const svgEl = document.querySelector(".js-pie-chart-svg");
  const vocesCountEl = document.querySelector(".js-chart__voices-count");

  if (svgEl === null) throw new Error("svg with class .js-pie-chart-svg not found");

  const legendNames = ["bad", "satisfactorily", "good", "verygood"];

  const { items, sumValues } = parseChartValues(legendNames);
  const groupOfPaths = createGroupOfPaths(items);

  svgEl.append(groupOfPaths);
  vocesCountEl.innerHTML = `${sumValues}`;
}
