import DateRange from "../date_range/index";
import Dropdown, { types } from "../dropdown/index";

import "../buttons";

import "./style.scss";

const _submitFormHandler = (e) => {
  e.preventDefault();
  e.stopPropagation();

  /* eslint-disable no-console */
  console.log("submit form");
};

const _priceFormat = (price) => price
  .toString()
  .split("")
  .reverse()
  .map((ch, index) => ((index + 1) % 3 === 0 ? ` ${ch}` : ch))
  .reverse()
  .join("");

export default class RoomDetailForm {
  constructor({ classId }) {
    this.classId = classId;

    this.numberOfDaysClass = "js-room-detail-form__number-of-days";
    this.costPerDaysClass = "js-room-detail-form__cost-per-days-cost";
    this.resultCostPerDayClass = "js-room-detail-form__cost-per-days-result-cost";

    this.discountSumClass = "js-room-detail-form__discount-sum";
    this.additionalFeeClass = "js-room-detail-form__additional-fee-sum";
    this.resultSumClass = "js-room-detail-form__result-value";

    this.topCostClass = "js-room-detail-form__top-cost-value";
  }

  start() {
    this._domInit();

    this._eventsInit();

    this.state = {
      roomCost: parseInt(this.topCostEl.innerHTML, 10),
      discountSum: parseInt(this.discountSumEl.innerHTML, 10),
      additionalFee: parseInt(this.additionalFeeEl.innerHTML, 10),
      numberOfDays: 0,
      result: 0,
    };

    this._formatCosts();

    this.dropdown = new Dropdown({
      classId: `${this.classId}-dropdown`,
      type: types.guests,
      defaultMessage: "Сколько гостей",
    });
    this.dropdown.start();

    const dateRange = new DateRange({
      classId: `${this.classId}-date-range`,
      onCalendarOpen: this._dateRangeCalendarOpenHandler.bind(this),
      onSelected: this._dateRangeSelectHandler.bind(this),
      onClean: this._dateRangeCleanHandler.bind(this),
    });
    dateRange.start();
  }

  _domInit() {
    const rootEl = document.querySelector(`.${this.classId}`);
    if (rootEl === null) { throw new Error(`ResultRoomRange error: not found element with class .${this.classId}`); }

    this.formEl = document.querySelector(`.${this.classId} .room-detail-form__form`);

    this.numberOfDaysEl = document.querySelector(`.${this.classId} .${this.numberOfDaysClass}`);
    this.costPerDaysEl = document.querySelector(`.${this.classId} .${this.costPerDaysClass}`);
    this.resultCostPerDayEl = document.querySelector(`.${this.classId} .${this.resultCostPerDayClass}`);

    this.discountSumEl = document.querySelector(`.${this.classId} .${this.discountSumClass}`);
    this.additionalFeeEl = document.querySelector(`.${this.classId} .${this.additionalFeeClass}`);
    this.resultSumEl = document.querySelector(`.${this.classId} .${this.resultSumClass}`);

    this.topCostEl = document
      .querySelector(`.${this.classId} .${this.topCostClass}`);
  }

  _eventsInit() {
    this.formEl.addEventListener("submit", _submitFormHandler);
  }

  _setState(newState) {
    this.state = Object.assign(this.state, newState);
    this._render();
  }

  _render() {
    this.numberOfDaysEl.innerHTML = this.state.numberOfDays;
    this.resultCostPerDayEl.innerHTML = _priceFormat(this._calculateCostPerDays());

    this.resultSumEl.innerHTML = this._calculateResultCost();
  }

  _formatCosts() {
    this.topCostEl.innerHTML = _priceFormat(this.topCostEl.innerHTML);
    this.discountSumEl.innerHTML = _priceFormat(this.discountSumEl.innerHTML);
    this.resultCostPerDayEl.innerHTML = _priceFormat(this.resultCostPerDayEl.innerHTML);
    this.costPerDaysEl.innerHTML = _priceFormat(this.state.roomCost);
  }

  _dateRangeCalendarOpenHandler() {
    this.dropdown.hideDropdown();
  }

  _dateRangeSelectHandler({ dateLeft, dateRight }) {
    const numberOfDays = (
      new Date(dateRight).getTime() - new Date(dateLeft).getTime()
    ) / (1000 * 60 * 60 * 24);

    this._setState({ numberOfDays });
  }

  _dateRangeCleanHandler() {
    this._setState({
      numberOfDays: 0,
    });
  }

  _calculateCostPerDays() {
    return this.state.numberOfDays * this.state.roomCost;
  }

  _calculateResultCost() {
    return this.state.numberOfDays === 0 ? 0 : _priceFormat(
      this._calculateCostPerDays() - this.state.discountSum + this.state.additionalFee,
    );
  }
}
